import React, { useState } from 'react';
import { create, saveToken } from '../blockchain/functions';
import {
  Box,
  Text,
  Input,
  NumberInput,
  NumberInputField,
  Button,
  Spinner,
  Switch,
} from '@chakra-ui/react';

function NewTokenForm() {
  const [tokenDetails, setTokenDetails] = useState({
    name: '',
    symbol: '',
    maxSupply: '',
    decimals: '',
    isReiussable: true,
    tokenReceiver: '',
    isBurnable: true,
  });
  const [isLoading, setIsLoading] = useState(false);

  const handleCreate = async () => {
    setIsLoading(true);
    let result = await create(tokenDetails);
    if (result?.events?.tokenDeploy.returnValues.newTokenAddress) {
      window.alert(
        `New token created on the address ${result.events.tokenDeploy.returnValues.newTokenAddress}`
      );
      saveToken(tokenDetails, result);
    }
    console.log(result);
    setIsLoading(false);
  };
  return (
    <Box textAlign="left" fontSize="md">
      <Text fontSize="small">Token Name:</Text>
      <Input
        value={tokenDetails.name}
        onChange={e =>
          setTokenDetails({ ...tokenDetails, name: e.target.value })
        }
        placeholder="Token Name"
      />
      <Text fontSize="small">Token Symbol:</Text>
      <Input
        value={tokenDetails.symbol}
        onChange={e =>
          setTokenDetails({ ...tokenDetails, symbol: e.target.value })
        }
        placeholder="Token Symbol"
      />
      <Text fontSize="small">Max Supply (0 = unlimited supply):</Text>
      <NumberInput>
        <NumberInputField
          value={tokenDetails.maxSupply}
          onChange={e =>
            setTokenDetails({
              ...tokenDetails,
              maxSupply: e.target.value,
            })
          }
          placeholder="Max Supply"
        />
      </NumberInput>
      <Text fontSize="small">Decimals:</Text>
      <NumberInput>
        <NumberInputField
          value={tokenDetails.decimals}
          onChange={e =>
            setTokenDetails({
              ...tokenDetails,
              decimals: e.target.value,
            })
          }
          placeholder="Decimals"
        />
      </NumberInput>

      <Text fontSize="small">
        Reiussable (true = token can be minted / false = no mint function):
      </Text>
      <Switch
        defaultChecked="true"
        value={tokenDetails.isReiussable}
        onChange={e => {
          setTokenDetails({
            ...tokenDetails,
            isReiussable: !tokenDetails.isReiussable,
          });
          console.log(tokenDetails.isReiussable);
        }}
      />
      {tokenDetails.isReiussable || (
        <>
          <Text fontSize="small">
            Token receiver address (if token NOT reiussable, all token supply
            will be send to this address):
          </Text>
          <Input
            value={tokenDetails.tokenReceiver}
            onChange={e =>
              setTokenDetails({
                ...tokenDetails,
                tokenReceiver: e.target.value,
              })
            }
            placeholder="Token Receiver Address"
          />
        </>
      )}

      <Text fontSize="small">
        Burnable (true = token can be burnend / false = no burn function):
      </Text>
      <Switch
        defaultChecked="true"
        value={tokenDetails.isBurnable}
        onChange={e => {
          setTokenDetails({
            ...tokenDetails,
            isBurnable: !tokenDetails.isBurnable,
          });
          console.log(tokenDetails.isBurnable);
        }}
      />
      <br />
      {isLoading ? (
        <Button marginTop="3" onClick={handleCreate}>
          <Spinner />
        </Button>
      ) : (
        <Button marginTop="3" onClick={handleCreate}>
          Create
        </Button>
      )}
    </Box>
  );
}

export default NewTokenForm;
