import React, { useState, useEffect } from 'react';
import { getTokens } from '../blockchain/functions';
import { Table, Thead, Tr, Th, Tbody, Td } from '@chakra-ui/react';

function TokenList() {
  const [tokens, setTokens] = useState([]);

  const getTokensInfo = async () => {
    let data = await getTokens();
    let result = [];
    data.map(token => {
      return result.push(token.attributes);
    });
    setTokens(result);
  };

  useEffect(() => {
    getTokensInfo();
  }, []);

  return (
    <Table variant="simple">
      <Thead>
        <Tr>
          <Th>Name</Th>
          <Th>Symbol</Th>
          <Th>Max Supply</Th>
          <Th>Decimals</Th>
          <Th>Reiussable</Th>
          <Th>Address</Th>
          <Th>Burnable</Th>
        </Tr>
      </Thead>
      <Tbody>
        {tokens.map(token => {
          const {
            address,
            decimals,
            isReiussable,
            maxSupply,
            name,
            symbol,
            isBurnable,
          } = token;
          return (
            <Tr key={address}>
              <Td>{name} </Td>
              <Td>{symbol} </Td>
              <Td>{maxSupply} </Td>
              <Td>{decimals} </Td>
              <Td>{isReiussable} </Td>
              <Td>{address} </Td>
              <Td>{isBurnable} </Td>
            </Tr>
          );
        })}
      </Tbody>
    </Table>
  );
}

export default TokenList;
