import deployer from './interface/deployer';
// import erc20 from './interface/erc20';
import web3, { accountAddress } from './web3';
const Moralis = require('moralis');

Moralis.initialize('91M5UMZcKjdRbjbS8FMlIAD3xk39cQq2K0C6adPa');

Moralis.serverURL = 'https://x9otfdizv1zu.moralisweb3.com:2053/server';

export const login = async () => {
  try {
    let newUser = await Moralis.Web3.authenticate();

    return newUser.get('ethAddress');
  } catch (error) {
    console.log(error.message);
  }
};

export const create = async data => {
  let instance = await deployer();
  let {
    name,
    symbol,
    maxSupply,
    decimals,
    isReiussable,
    tokenReceiver,
    isBurnable,
  } = data;
  try {
    if (isReiussable) {
      tokenReceiver = accountAddress;
    }
    let result = await instance.methods
      .createToken(
        name,
        symbol,
        web3.utils.toWei(maxSupply),
        decimals,
        isReiussable,
        tokenReceiver,
        isBurnable
      )
      .send({ from: accountAddress });
    return result;
  } catch (error) {
    window.alert(error.message);
  }
};

export const saveToken = async (data, result) => {
  const Token = Moralis.Object.extend('Token');
  const token = new Token();

  token.set('name', data.name);
  token.set('symbol', data.symbol);
  token.set('maxSupply', data.maxSupply);
  token.set('decimals', data.decimals);
  token.set('isReiussable', data.isReiussable.toString());
  token.set('address', result.events.tokenDeploy.returnValues.newTokenAddress);
  token.set('isBurnable', data.isBurnable.toString());

  token.save().then(
    token => {
      // Execute any logic that should take place after the object is saved.
      window.alert('New object created with objectId: ' + token.id);
    },
    error => {
      // Execute any logic that should take place if the save fails.
      // error is a Moralis.Error with an error code and message.
      window.alert(
        'Failed to create new object, with error code: ' + error.message
      );
    }
  );
};

export const getTokens = async () => {
  const Token = Moralis.Object.extend('Token');
  const query = new Moralis.Query(Token);

  const response = await query.find();

  return response;
};
